@media (max-width: 820px) {
.burger_menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 42px;
    height: 32px;
    background-color: #349ae0;
    border-radius: 2px;
    z-index: 1500; }
    .burger_menu:before {
      content: '';
      background-color: white;
      position: absolute;
      width: 50%;
      height: 2px;
      left: 10px;
      top: 10px;
      transition: all 0.3s ease 0s; }
    .burger_menu span {
      position: absolute;
      left: 10px;
      width: 50%;
      background-color: white;
      top: 16px;
      height: 2px;
      transition: all 0.3s ease 0s; }
    .burger_menu:after {
      content: '';
      background-color: white;
      position: absolute;
      width: 50%;
      height: 2px;
      left: 10px;
      top: 22px;
      transition: all 0.3s ease 0s; }
  .header_menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 400px;
    background-color: white;
    transition: all 0.3s ease 0s; }
    .header_menu ul {
      padding-top: 44px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center; }
  .burger_menu.active:before {
    transform: rotate(45deg);
    top: 16px; }
  .burger_menu.active:after {
    transform: rotate(-45deg);
    top: 16px; }
  .burger_menu.active span {
    transform: scale(0); }
  .header_menu.active {
    top: 0; 
} }

@media (max-width: 710px) {
